import scratchDescriptionImage from '../assets/scratch-description.jpeg';
import vpl3DescriptionImage from '../assets/vpl3-description.jpeg';

export const photoSelector = (type: 'vpl3' | 'scratch') => {
    const selectorPhoto: { vpl3: string; scratch: string } = {
        vpl3: vpl3DescriptionImage,
        scratch: scratchDescriptionImage,
    };
    return selectorPhoto[type] ?? selectorPhoto['vpl3'];
};