/* eslint-disable no-template-curly-in-string */
/* eslint-disable dot-notation */
/* eslint-disable no-undef */
import React, {useEffect, useState} from 'react';
import './App.css';
import {INode, createClient, IGroup} from '@mobsya-association/thymio-api';
import {Icon} from './Icons';

import {Observable, createObservable, subscribe} from './observables';
import {toJS} from 'mobx';

import {
  styles,
  statusColors,
  Spinner,
  ReloadButton,
  webViewContainer,
  rootContainer,
  photoDescriptionContainer,
  containerDescription,
  containerBody,
  containerBodyTablet,
  spinnerContainer,
  nodeName,
} from './styles';
import {textSelector} from './traductions';
import {linkSelector} from './helpers/linkSelector';
import {useQueryParams} from './helpers/useQueryParams';
import {photoSelector} from './helpers/photoSelector';
import {useScreen} from './helpers/useScreen';
import {tabletContainer} from './styles';

type ThymioRobot = {
  name: string;
  status: string;
  nodeId: string;
  type: string;
  ip: string;
};

class Nodes {
  nodes: Observable<{[host: string]: ThymioRobot[]}> = createObservable({
    key: 'all services nodes robots found',
    initialValue: {},
  });

  addNode(node: ThymioRobot[], host: string) {
    const tempHost = this.nodes.state[host] ?? [];
    const tempHostObject: {[key: string]: any} = tempHost.reduce(
      (acc, val) => ({...acc, [val.nodeId]: val}),
      {},
    );

    node.forEach(val => {
      tempHostObject[val.nodeId] = val;
    });

    this.nodes.set({
      ...this.nodes.state,
      [host]: Object.values(tempHostObject).filter(
        val => val.status !== 'disconnected',
      ),
    });
  }

  onChange = (fun: (args: {[host: string]: ThymioRobot[]}) => void) => {
    subscribe(this.nodes, () => {
      fun(toJS(this.nodes.state));
    });
  };

  getNodes() {
    return toJS(this.nodes.state);
  }
}

function App() {
  const {width, height, orientation} = useScreen();
  const [wsHost, setWsHost] = useState<
    Array<{host: string; hostName: string; ip: string; addresses: string[]}>
  >([]);
  const {queryParams} = useQueryParams();
  const [allNodes, setAllNodes] = useState<{[host: string]: ThymioRobot[]}>({});
  const [zeroconfNotifed, setZeroconfNotifed] = useState<any>({});
  const [isWaiting, setIsWaiting] = useState<boolean>(false);

  const nodes = new Nodes();

  const {title, description, list, help, visit, common} = textSelector(
    queryParams.gl.interface,
    queryParams.lang,
  );

  const findThymioInLocalNetwork = (data: any) => {
    setZeroconfNotifed(data);
    const tempWsHost = Object.values(data).map(
      ({txt, addresses, host}: any) => {
        const ip = addresses;
        return {
          host: `ws://${ip}:${txt['ws-port']}`,
          hostName: txt['name'],
          ip,
          addresses,
        };
      },
    );
    setWsHost(tempWsHost);
  };

  const reloadScan = async () => {
    if (!isWaiting) {
      // Verify that the scan is not already in progress
      setIsWaiting(true); // Set the waiting flag to true

      setTimeout(() => {
        findThymioInLocalNetwork(zeroconfNotifed); // Re-run the scan
        setIsWaiting(false); //  Set the waiting flag to false
      }, 3000); // Wait 3 seconds before re-running the scan
    } else {
      console.log('a findThymioInLocalNetwork is already in progress');
    }
  };

  const sendMessageToReactNative = (message: string) => {
    // Send a message to the React Native WebView
    if (window.ReactNativeWebView) {
      window.ReactNativeWebView.postMessage(message);
    } else {
      console.error('window.ReactNativeWebView no est definido.');
    }
  };

  const handleClickOnNode = (node: ThymioRobot) => {
    if (node.status !== 'available') {
        return;
    }
    const fullUrl = linkSelector(
      node.nodeId,
      node.ip,
      queryParams.gl.interface,
      node.name,
      queryParams.lang,
    );
    sendMessageToReactNative(
      JSON.stringify({
        spec: 'openUrl',
        url: fullUrl,
      }),
    );
    // window.open(fullUrl);
  };

  useEffect(() => {
    wsHost.forEach(({host, ip, addresses, hostName}: any) => {
      const connectClient = createClient(host);
      connectClient.onNodesChanged = async (nodesRobots: INode[]) => {
        const _nodes: any = await Promise.all(
          nodesRobots.map(async (node: INode) => {
            console.log(`Found ${node.name} ${node.id} `);
            const nodeId = node.id.toString().replace(/[^a-zA-Z0-9 -]/g, '');
            const {name} = node;
            node.watchSharedVariablesAndEvents(true);

            node.onGroupChanged = (group: IGroup) => {};

            node.onStatusChanged = () => {};

            return {
              nodeId,
              name,
              status: node.statusAsString,
              type: node.typeAsString,
              ip,
              addresses,
              hostName,
            };
          }),
        );

        nodes.addNode(_nodes, host);
      };
    });
  }, [wsHost]);

  useEffect(() => {
    if (queryParams.data && queryParams.status !== 'stoped') {
      findThymioInLocalNetwork(queryParams.data);
    }
  }, [queryParams]);

  useEffect(() => {
    nodes.onChange(_nodes => {
      setAllNodes(_nodes);
    });
  }, [nodes.nodes]);

  return (
    <div style={rootContainer}>
      <div style={{...tabletContainer(queryParams.isTablet)}}>
        <img
          src={photoSelector(queryParams.gl.interface)}
          style={photoDescriptionContainer}
          alt="Thymio photo programming language"
        />
        <div style={containerDescription}>
          <p style={{...styles.title, textAlign: 'justify'}}>{title}</p>
          <p style={{...styles.paragraphe, textAlign: 'justify'}}>
            {description}
          </p>
          <div style={styles.lu}>
            {list.map((item, index) => (
              <p key={index} style={{...styles.list, textAlign: 'justify'}}>
                 {item}
              </p>
            ))}
          </div>
          <p style={{...styles.paragraphe, textAlign: 'justify'}}>{help}</p>
          <p style={{...styles.paragraphe, textAlign: 'justify'}}>
            {visit.split('https://www.thymio.org').map((part, index) =>
              index === visit.split('https://www.thymio.org').length - 1 ? (
                <span key={index}>{part}</span>
              ) : (
                <React.Fragment key={index}>
                  <span>{part}</span>
                  <span
                    style={{
                      fontWeight: '400',
                      color: '#0A9EEB',
                      cursor: 'pointer',
                    }}
                    onClick={() =>
                      window.open('https://www.thymio.org', '_blank')
                    }>
                    https://www.thymio.org
                  </span>
                </React.Fragment>
              ),
            )}
          </p>
        </div>
      </div>

      <div style={{...containerBodyTablet(queryParams.isTablet)}}>
        <div style={containerBody}>
          {isWaiting ? (
            <>
              <div style={spinnerContainer}>
                <Spinner />
                <br />
                <div>{common.rescanInfo}</div>
              </div>
            </>
          ) : (
            <>
              <ReloadButton onClick={reloadScan}>
                {common.reload_scan}
              </ReloadButton>
              {Object.values(allNodes)
                .reduce((acc, val) => acc.concat(val), [])
                .map((node: ThymioRobot, index: number) => (
                  <div
                    key={index}
                    style={webViewContainer(node)}
                    onClick={() => handleClickOnNode(node)}>
                    <Icon
                      name="thymio"
                      sizeH={60}
                      sizeW={100}
                      palleteFill={[...(statusColors[node.status] ?? [])]}
                    />
                    <div style={nodeName}>{node.name}</div>
                  </div>
                ))}
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
