import { useEffect, useState } from "react";

export const useQueryParams = () => {
    const [queryParams, setQueryParams] = useState<{
        data: string;
        lang: string;
        status: string;
        isTablet: string;
        gl: { interface: 'vpl3' | 'scratch' };
    }>({ data: '', lang: 'en', isTablet: '', status: '', gl: { interface: 'vpl3' } });

    useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const queryParamsObj = {
            data: '',
            lang: 'en',
            status: '',
            gl: '',
            isTablet: '',
        };
        params.forEach((value, key) => {
            queryParamsObj[key as keyof typeof queryParamsObj] = value;
        });
        setQueryParams({
            data: queryParamsObj.data !== '' ? JSON.parse(queryParamsObj.data) : {},
            lang: queryParamsObj.lang,
            status: queryParamsObj.status,
            isTablet: queryParamsObj.isTablet,
            gl: queryParamsObj.gl !== '' ? JSON.parse(queryParamsObj.gl) : {},
        });
    }, []);

    return { queryParams };
};